.background-container {
  display: flex;
  flex-direction: row;
  background-color: rgba(194, 192, 192, 0.549);
}

.content-container {
  flex-grow: 1;
}

.section__padding {
  padding: 2rem 2rem;
}

.section__margin {
  margin: 2rem 2rem;
}

