.blur_background {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 100px;
  background: var(--globalnav-background);
  backdrop-filter: var(--globalnav-backdrop-filter);
  -webkit-backdrop-filter: var(--globalnav-backdrop-filter);

  /* filter: blur(50px);
  -webkit-filter: blur(50px); */
  z-index: 99;
}
.navbar-container {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 1.5rem 2rem;
}

.navbar-links {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0;
}

.logo {
  color: var(--Black, #373737);
  font-size: 23px;
  font-weight: bolder;
  line-height: 26px;
  letter-spacing: -0.23px;
}

.filled-btn {
  background-color: var(--global-green, #1b4715);
  color: var(--global-white, #f6f6f6);
  border: none;
  border-radius: 16px;
  margin: 0rem 0.5rem;
  font-size: 1rem;
  font-weight: 600;
  line-height: 1.5rem;
  letter-spacing: -0.5px;
  height: 48px;
  width: 132px;
  cursor: pointer;
}

.dotted-btn {
  background-color: transparent;
  color: var(--global-green, #1b4715);
  border: 1px solid var(--global-green, #1b4715);
  border-radius: 16px;
  margin: 0rem 0.5rem;
  font-size: 1rem;
  font-weight: 600;
  line-height: 1.5rem;
  letter-spacing: -0.5px;
  height: 48px;
  width: 132px;
  cursor: pointer;
}
